<script>
import appConfig from '@src/app.config'
import Layout from '@layouts/main'
import PageHeader from '@components/page-header'
import axios from 'axios';
import Utils from '../../../../utils/Util';

/**
 * Invoice component
 */
export default {
	page: {
		title: 'Log User',
	},
	components: { Layout, PageHeader },
	data() {
		return {
			title: 'Log User',
			api: Utils.Host(),
			message: "",
			data:[],
			options: "",
			alert:false,
			datatahun: [],
			modeltahun:"",
			modeltampil: "100",
			datatampil: [],
			modelsearch: "",
		}
	},
	mounted() {
      console.log("alamat", Utils.Host())
	  this.Getfilter()
      this.Getdata()
    },
	computed: {
      pilihmodeltahun () {
            return this.Getdata(this.modeltahun)
      },
      pilihmodeltampil() {
        return this.Getdata(this.modeltahun, this.modeltampil);
      },
      pilihmodelsearch() {
        return this.Getdata(this.modeltahun, this.modeltampil, this.modelsearch);
      },
    },
	methods: {
      async Getdata(tahun, tampil, search) {
        try {
            let f_tahun = tahun ? tahun : new Date().getFullYear();
            let kodetampil = tampil ? tampil : 100;
            let kodesearch = search ? search : "";
            let response = await axios.get(this.api+"/log/index?tahun="+f_tahun +
            "&tampil=" +
            kodetampil +
            "&search=" +
            kodesearch, Utils.authHeader());
            this.data = response.data;
        } catch (err) {
            console.error(err);
        }
      },
	  async Getfilter() {
        try {
           let tahun = [];
            let i = new Date().getFullYear();
            do {
                tahun.push(i);
                i--;
            } while (i > 2001);
            this.datatahun = tahun;
        } catch (err) {
            console.error(err);
        }
      },
	}
}
</script>

<template>
	<Layout>
		<PageHeader :title="title" :items="items" class="d-print-none" />
		<div class="row">
			<div class="col-12">
				<div class="card">
					<div class="card-body">
						<!-- Logo & title -->
						
						<div class="row">
							<div class="col-12">
								<div class="table-responsive">
									<br>&nbsp;&nbsp;
									<b-alert
											v-model="alert"
											variant="danger"
											dismissible
											>{{ message }}</b-alert
										>
									<br>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Tahun"
										label-for="input-horizontal"
									>
									<b-form-select v-model="modeltahun" class="mb-2" :options="datatahun"  @click="pilihmodeltahun">
									</b-form-select>
									</b-form-group>
									
									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Tampil"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="modeltampil"
										type="number"
										required
										@click="pilihmodeltampil"
									></b-form-input>
									</b-form-group>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Data / Username"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="modelsearch"
										type="text"
										required
										@click="pilihmodelsearch"
									></b-form-input>
									</b-form-group>

									<div class="table-responsive">
									<table class="table mb-0">
										<thead>
										<tr>
											<th scope="col">No</th>
											<th scope="col">Data</th>
											<th scope="col">Tanggal</th>
											<th scope="col">Username</th>
										</tr>
										</thead>
										<tbody>
										<tr v-for="(tampil,index) in data" :key="tampil.f_tgl_create">
											<th scope="row">{{index+1}}</th>
											<td>{{tampil.f_data}}</td>
											<td>{{tampil.f_tgl_create}}</td>
											<td>{{tampil.f_username}}</td>
										</tr>
										</tbody>
									</table>
									</div>

								</div>
								<!-- end table-responsive -->
							</div>
							<!-- end col -->
						</div>
						<!-- end row -->

	
					</div>
				</div>
			</div>
			<!-- end col -->
		</div>
		<!-- end row -->
		<!-- end row -->
	</Layout>
</template>
